<template>
	<div class="d-box v-box">
		<div class="headDiv">
			<div style="padding: 20px; height: auto;float: left;">
				<Button :type='add.type' :icon='add.icon' :text='add.text' @click="addClick"></Button>
				<Button :type='edit.type' :icon='edit.icon' :text='edit.text' @click="editClick"></Button>
			</div>
		</div>
		<div class="flexTable">
			<Table :data='rows' :tableName='tableName' @selection-change="handleSelectionChange" :currentPage1="currentPage1" :num='num' @size-change="handleSizeChange" @current-change="handleCurrentChange"></Table>
		</div>

		<el-dialog :title="title" :visible.sync="plus" width="600px" center>
			<el-form class="clearfix" :model="form">
				<el-form-item label="选择设备" :label-width="formLabelWidth">
					<el-select class="selectwidth" v-model="form.terminal" placeholder="请选择设备">
						<el-option v-for="item in system" :key="item" :label="item" :value="item"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item class="lastformItem" label="版本号" :label-width="formLabelWidth">
					<el-input v-model="form.version" placeholder="请输入版本号" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item>
					<el-checkbox-group v-model="form.isLow">
						<el-checkbox label="是否是最低版本">
						</el-checkbox>
					</el-checkbox-group>
				</el-form-item>

			</el-form>
			<div slot="footer" class="dialog-footer">
				<SolidButton :type='"hollow"' :text='"取消"' @click="plus = false"></SolidButton>
				<SolidButton :type='"solid"' :text='"确定"' @click="confirm"></SolidButton>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	import Button from '../../button/Button.vue';
	import SolidButton from '../../SelfControls/solidButton/solidButton.vue';
	import Table from '../../table/Table.vue';
	export default {
		components: {
			Button,
			SolidButton,
			Table,
		},
		data() {
			return {
				currentPage1: 1,
				rows: [],
				list: [],
				system: [], //选择设备
				plus: false,
				id: '',
				title: '',
				num: 0, //jxs会报一个类型错误，就设了一个数字初始值
				pageNum: '1',
				pageRows: '10',
				form: {
					terminal: '',
					version: '',
					isLow: ''
				},
				formLabelWidth: '70px',
				//头部按钮
				add: {
					text: '新增', //按钮名称
					type: 'icon-add', //按钮颜色class,及图标
				},
				edit: {
					text: '编辑', //按钮名称
					type: 'icon-edit', //按钮颜色class,及图标
				},
				//表格对应的字段
				tableName: [{
						value: 'terminal',
						name: "设备名称"
					},
					{
						value: 'version',
						name: "版本号"
					},
					{
						value: 'isLow',
						name: '是否是最低版本'
					},
					{
						value: 'updateTime',
						name: '更新时间'
					},
				]
			}
		},
		methods: {
			//表格Checkbox 选中
			handleSelectionChange(val) {
				this.list = val;
			},
			//点击行选中复选框
			toggleSelection(row) {
				var rows = [];
				rows.push(row);
				if(rows) {
					rows.forEach(row => {
						this.$refs.multipleTable.toggleRowSelection(row);
					});
				} else {
					this.$refs.multipleTable.clearSelection();
				}
			},
			//新增
			addClick() {
				this.title = '新增菜单';
				this.plus = true;
				this.form.terminal = '';
				this.form.version = '';
				this.followed = true;
			},
			//编辑
			editClick() {
				if(this.list.length == 0) {
					this.$message({
						message: '至少选择一条数据',
						type: 'warning'
					});
				} else if(this.list.length > 1) {
					this.$message({
						message: '只能选择一条数据',
						type: 'warning'
					});
				} else {

					this.title = '编辑菜单';
					this.plus = true;
					this.form.terminal = this.list[0].terminal;
					this.form.version = this.list[0].version;
					this.id = this.list[0].id
					this.$api.post('/crm/version/getTerminalList', {}, r => {
						this.menu = r.repData
					}, e => {
						console.log(e);
					})
				}

			},
			//初始化数据
			loadData() {
				var self = this;
				var p = {
					type: "0",
					pageNum: this.pageNum,
					pageRows: this.pageRows
				}
				self.$api.post('/crm/version/doVersionList', p, r => {
					self.rows = r.repData.list
					self.num = r.repData.total
				}, e => {
					console.log(e);
				})
				self.$api.post('/crm/version/getTerminalList', {}, r => {
					self.system = r.repData
				}, e => {
					console.log(e);
				})
			},
			//分页
			handleSizeChange(val) {
				this.pageRows = val;
				this.loadData();
			},
			handleCurrentChange(val) {
				this.pageNum = val;
				this.loadData();
			},
			//新增确认
			confirm() {
				if(this.form.terminal == '') {
					this.$message({
						message: '请选择设备',
						type: 'warning'
					});
					return;
				}
				if(this.form.version == '') {
					this.$message({
						message: '请输入版本号',
						type: 'warning'
					});
					return;
				}

				var str = '';
				var url = '';
				if(this.title == '新增菜单') {
					str = '新增成功';
				} else if(this.title == '编辑菜单') {
					str = '修改成功';
				}
				var p = {
					id: this.id,
					terminal: this.form.terminal,
					version: this.form.version,
					isLow: this.form.isLow ? 1 : 0
				}
				this.$api.post('crm/version/doVersionAddOrUpdate', p, r => {
					this.$message({
						message: str,
						type: 'success'
					});
					this.plus = false;
					this.loadData();
				}, e => {
					console.log(e);
				})
			},
			//新增选择设备触发事件
			//			chose() { 
			//				var self =this;
			//				self.id = self.form.id
			//				var p = {
			//					type: 0,
			//					id: this.form.id
			//				}
			//				self.$api.post('/crm/version/getTerminalList', p, r => {
			//					self.system = r.repData
			//				}, e => {
			//					console.log(e);
			//				})
			//			}
		},
		created() {
			this.loadData();
		},
	}
</script>

<style scoped>
	.el-form-item {
		margin-bottom: 22px;
		width: 50%;
		float: left;
	}
	
	.el-select {
		width: 100%;
	}
	
	.dialog-footer {
		text-align: center;
	}
	
	.el-form {
		float: left;
	}
	
	.el-form .el-form-item:nth-child(3) {
		clear: both;
	}
	
	.maun {
		float: right;
	}
	/*2\列表头部按钮*/
	
	.headerButton {
		height: 70px;
		background: #fff;
		/*padding: 0 20PX;*/
		float: left;
	}
	
	.iconDiv {
		margin-left: 10px;
		float: left;
		width: 50px;
		height: 50px;
		text-align: center;
		line-height: 50px;
		border-radius: 50%;
	}
	
	.iconDiv i {
		width: 21px;
		height: 21px;
		font-size: 15px;
		color: white;
	}
	
	.iconTitle {
		font-size: 10px;
		line-height: 22px;
	}
	
	.d-box {
		display: -moz-box;
		/* OLD - Firefox 19- (buggy but mostly works) */
		display: -webkit-box;
		/* OLD - iOS 6-, Safari 3.1-6 */
		display: -webkit-flex;
		/* NEW - Chrome */
		display: -ms-flexbox;
		/* TWEENER - IE 10 */
		display: box;
		display: flexbox;
		display: flex;
		/* NEW, Spec - Opera 12.1, Firefox 20+ */
		height: 100%;
		width: 100%;
	}
	
	.v-box {
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		-moz-box-orient: vertical;
		-moz-box-direction: normal;
		-webkit-flex-direction: column;
		-moz-flex-direction: column;
		-ms-flex-direction: column;
		-o-flex-direction: column;
		flex-direction: column;
	}
	
	.headDiv {
		/*padding: 20px;*/
		background: #fff;
		overflow: hidden;
		position: relative;
	}
	
	.flexTable {
		-webkit-box-flex: 1;
		-moz-box-flex: 1;
		-ms-flex: 1;
		flex: 1;
		position: relative;
	}
	
	.tableDiv {
		width: 100%;
		height: 100%;
		position: absolute;
		overflow: auto;
	}
	
	.footerDiv {
		height: 40px;
		position: relative;
	}
	
	/*3、窗口*/
	
	.el-form-item {
		width: 49%;
		float: left;
	}
	
	.lastformItem {
		margin-left: 10px;
	}
	
	.selectwidth{
		width: 199.5px;
	}
</style>


